<template>
	<view class="content">
		<u-navbar :is-back="true" title="户型详情" 
		title-color='#fff'
		back-icon-color='#fff' 
		:background="{ background:'#213244'}">
		</u-navbar>
		<image :src='hx.img' :lazy-load='true' class="logo">
		<view class="biaoti f9b">
			<span>{{hx.title}}</span>
		</view>
		<view class="mianji ">
			<span>建筑面积：{{hx.architecture}}</span>
		</view>
		<view class="jiesao">
			<span>户型介绍：{{hx.content}}</span>
		</view>
	</view>
</template>

<script>
	import api from '../../api/index.js'
	import container from '../../components/container/index.vue'
	import LsSwiper from '../../components/ls-swiper/index.vue'
	import MescrollMixin from "@/components/mescroll-uni/mescroll-mixins.js";
	import MescrollBody from "@/components/mescroll-uni/mescroll-body.vue";
	import {
		AkxhHd
	} from '../../api/akxhhd.js'
	export default {
		data() {
			return {
				hx:[],
			}
		},
		onLoad(option){
			this.init(option.id);
		},
		methods: {
			init(hid){
				this.AkxhHdlists(hid);
			},
			AkxhHdlists(hid){
				AkxhHd('/catalyzer/AkxhHx/query-by-id','GET',{'id':hid}).then(res => {
					if (res.data.code == 200) {
						let result = res.data.result;
						result.img = this.imgURL + res.data.result.img;
						this.hx = result;
					}
				})
			}
		}
	}
</script>

<style>
	.content {
	  background-color: #f8f8f8;
	}
	.content .logo{
		width: 100%;
		height: 400px;
	}
	.content .biaoti{
		font-size: 30px;
		margin-left:20px;
		/* border: #ff557f solid 5px; */
	}
	.content .mianji{
		font-size: 20px;
		margin-left:20px;
		/* border: #ff557f solid 5px; */
	}
	.content .jiesao{
		font-size: 17px;
		padding: 20px;
	}
	
</style>
